<template>
  <div class="user-data-psw">
    <jTitle>修改密码</jTitle>
    <el-form :model="userDataForm" :rules="rules" ref="userDataForm" label-width="120px" class="user-form">
      <el-form-item label="您的用户名：">
        {{realName}}
      </el-form-item>
      <el-form-item label="请输入旧密码：" prop="oldPassword" :error="errors.oldPassword">
          <el-input type="password"  maxlength="18"   size="small" v-model.number="userDataForm.oldPassword"></el-input>
      </el-form-item>
      <el-form-item label="请输入新密码" prop="newPassword">
          <el-input type="password"  minlength="6" maxlength="18"  size="small" v-model.number="userDataForm.newPassword"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码" prop="newPasswordRep">
          <el-input type="password"  minlength="6" maxlength="18"  size="small" v-model.number="userDataForm.newPasswordRep"></el-input>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <el-button type="primary" round size="small" @click="saveForm">保存</el-button>
      <el-button type="primary" round plain size="small" @click="back">取消</el-button>
    </div>
  </div>
</template>
<script>
  import {admin as api} from '../../api'
  import { jTitle } from '../../components'
  export default {
    data() {
      return {
        userDataForm: {
          oldPassword: '',
          newPassword: '',
          newPasswordRep: '',
          userType: ''
        },
        userName: '-',
        rules: {
          oldPassword: [
            { validator: this.psdOld, trigger: 'blur' }
          ],
          newPassword: [
            { validator: this.psd, trigger: 'blur' }
          ],
          newPasswordRep: [
            { validator: this.psdRepeat, trigger: 'blur' }
          ]
        },
        errors: {
          oldPassword: ''
        }
      }
    },
    components: {
      jTitle
    },
    methods: {
      back() {
        this.$router.back()
      },
      saveForm() {
        let that = this
        this.$refs['userDataForm'].validate((valid) => {
          if (valid) {
            api.updateSysUserZst(that.userDataForm).then(res => {
              if (res.code === 1) {
                that.$msgbox({
                  title: '系统提示',
                  message: '<i class="iconfont icon-correct"></i><span>' + res.msg + ',点击确认重新登陆</span>',
                  center: true,
                  roundButton: true,
                  customClass: 'messageBox',
                  type: 'success',
                  dangerouslyUseHTMLString: true,
                  callback: () => {
                    this.$store.dispatch('logout', '').then(res => {
                      this.$router.push({path: '/login'})
                    }).catch(err => {
                      console.log(err)
                    })
                  }
                })
              }
            }).catch(err => {
              this.$msgbox({
                title: '系统提示',
                message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
                center: true,
                roundButton: true,
                customClass: 'messageBox',
                type: 'success',
                dangerouslyUseHTMLString: true,
                callback: () => {}
              })
              this.errors.oldPassword = err.msg
            })
          } else {
            return false
          }
        })
      },
      submitForm() {
        this.errors.input1 = '密码输入错误，请重新输入'
      },
      psdOld(rule, value, callback) {
        if (value === '') {
          callback(new Error('请输入密码'))
        } else {
          this.errors.oldPassword = ''
          callback()
        }
      },
      psd(rule, value, callback) {
        let valueL = value.toString()
        if (value === '') {
          callback(new Error('请输入新密码'))
        }
        if (valueL.length < 6) {
          callback(new Error('请输入6位及以上的数字、字母或符号等字符！'))
        } else if (valueL.length > 18) {
          callback(new Error('请输入18位以下的数字、字母或符号等字符！'))
        } else {
          callback()
        }
      },
      psdRepeat(rule, value, callback) {
        if (value === '') {
          callback(new Error('请再次输入密码'))
        } else if (value !== this.userDataForm.newPassword) {
          callback(new Error('确认密码与新密码不一致!'))
        } else {
          callback()
        }
      }
    },
    computed: {
      realName() {
        return this.$store.state.user.userInfo.userName
      }
    },
    created() {
      this.userDataForm.userType = this.$store.state.user.userInfo.userType
      if (this.$store.state.user.userInfo.loginCount === 1) {
        this.userDataForm.oldPassword = this.$store.state.user.userInfo.tpsw
      }
    }
  }
</script>
<style lang="scss" scoped>
  .user-data-psw{
    box-sizing:border-box;

    .user-form{
      width:400px;
      margin: auto;

    }
    .form-submit{
      text-align: center;
      margin-top:80px;
    }

    & /deep/ .el-form-item.is-required .el-form-item__label:before{
        content: '';
        color: #f56c6c;
        margin-right: 4px;
      }
    & /deep/ .el-form-item .el-input:after{
        content: '*';
        color: #f56c6c;
        margin-right: 4px;
        position: absolute;
        right:-40px;
      }
  }
</style>
<style lang="scss">
    .user-data-psw{
      .el-form-item.is-required .el-form-item__label:before{
        content: '';
        color: #f56c6c;
        margin-right: 4px;
      }
      .el-form-item .el-input:after{
        content: '*';
        color: #f56c6c;
        margin-right: 4px;
        position: absolute;
        right:-40px;
      }
    }
</style>



