<template>
  <div class="user-base">
    <el-form label-width="90px" class="user-form" :model="userInfoForm" :rules="rules" ref="userInfoForm" >
      <el-form-item label="用户名：">
         <span v-if="userInfoData.userName">{{userInfoData.userName}}</span>
         <span v-else>-</span>
      </el-form-item>
      <el-form-item label="姓名：">
        <span v-if="userInfoData.userName">{{userInfoData.realName}}</span>
        <span v-else>-</span>
      </el-form-item>
      <el-form-item label="所属部门：">
        <span v-if="userInfoData.govName">{{userInfoData.govName}}</span>
        <span v-else>-</span>
      </el-form-item>
      <el-form-item label="联系人：" hidden>
          <el-input size="small" maxlength="18"  v-model="userInfoForm.realName"></el-input>
      </el-form-item>
      <el-form-item label="联系方式：" prop="userMobile">
          <el-input size="small" type="text"  v-model="userInfoForm.userMobile"></el-input>
      </el-form-item>
      <el-form-item label="企业地址：">
          <el-input size="small"  maxlength="36"  v-model="userInfoForm.phoneverificode"></el-input>
      </el-form-item>
    </el-form>
    <div class="form-submit">
      <el-button type="primary" round size="small" @click="upDate">保存</el-button>
    </div>
  </div>
</template>
<script>
  import {admin as api} from '../../api'
  import { jTitle } from '../../components'
  export default {
    data() {
      return {
        userInfoData: '',
        userInfoForm: {
          userMobile: '',
          realName: '',
          phoneverificode: ''
        },
        rules: {
          userMobile: [
            { validator: this.uMobile, trigger: 'blur' }
          ]
        }
      }
    },
    components: {
      jTitle
    },
    methods: {
      uMobile(rule, value, callback) {
        let mobile = /^[1][3,4,5,7,8][0-9]{9}$/
        let phone = /^0\d{2,3}-?\d{7,8}$/
        if (value === '') {
          callback()
        } else if (!mobile.test(value) && !phone.test(value)) {
          callback(new Error('请输入正确的手机号！'))
        } else {
          callback()
        }
      },
      upDate() {
        let that = this
        this.$refs['userInfoForm'].validate((valid) => {
          if (valid) {
            api.updateSysUser(that.userInfoForm).then(res => {
              if (res.code === 1) {
                this.$msgbox({
                  title: '系统提示',
                  message: '<i class="iconfont icon-correct"></i><span>' + res.msg + '</span>',
                  center: true,
                  roundButton: true,
                  customClass: 'messageBox',
                  type: 'success',
                  dangerouslyUseHTMLString: true
                })
              }
            }).catch(err => {
              this.$msgbox({
                title: '系统提示',
                message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
                center: true,
                roundButton: true,
                customClass: 'messageBox',
                type: 'success',
                dangerouslyUseHTMLString: true,
                callback: function() {
                  console.log('关闭')
                }
              })
            })
          } else {
            return false
          }
        })
      }
    },
    created() {
      let that = this
      api.userInfo('').then(res => {
        that.userInfoData = res.data
        that.userInfoForm.userMobile = res.data.userMobile
        that.userInfoForm.realName = res.data.realName
        that.userInfoForm.phoneverificode = res.data.phoneverificode
      }).catch(err => {
        this.$msgbox({
          title: '系统提示',
          message: '<i class="iconfont icon-tanhao"></i><span>' + err.msg + '</span>',
          center: true,
          roundButton: true,
          customClass: 'messageBox',
          type: 'success',
          dangerouslyUseHTMLString: true,
          callback: () => {}
        })
      })
    }
  }
</script>
<style lang="scss" scoped>
  .user-base{
    .user-form{
        width:400px;
        margin: auto;
      }
    .form-submit{
      text-align: center;
      margin-top:80px;
    }
  }
</style>



